//////////////////////
// transitions
//////////////////////

// @include transition-default;
@mixin transition-default {
  transition: all 300ms cubic-bezier(.63,.36,0,.97); // disable top and left animations
  &.fade-in {
  }
  &.fade-out {
  }
}

//////////////////////
// collapse
//////////////////////

.collapse-height,
.collapse-width {
  @include transition-default;
}
.collapse-width-left,
.collapse-width-right,
.collapse-height-top,
.collapse-height-bottom {
  @include transition-default;
}

//////////////////////
// anim
//////////////////////

// @include anim-opacity;
@mixin anim-opacity {
  @include transition-default;
  opacity: 0;
  &.fade-in {
    opacity: 1;
  }
  &.fade-out {
    opacity: 0;
  }
}
.anim-opacity {
  @include anim-opacity;
}

// @include anim-horizontal;
@mixin anim-horizontal {
  @include transition-default;
  opacity: 0;
  transform: translateX(-.618rem);
  &.fade-in {
    opacity: 1;
    transform: translateX(0);
  }
  &.fade-out {
    opacity: 0;
    transform: translateX(.618rem);
  }
}
.anim-horizontal {
  @include anim-horizontal;
}

// @include anim-horizontal-zoom;
@mixin anim-horizontal-zoom {
  @include transition-default;
  opacity: 0;
  transform: translateX(-.618rem) scale(.95);
  &.fade-in {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
  &.fade-out {
    opacity: 0;
    transform: translateX(.618rem) scale(.95);
  }
}
.anim-horizontal-zoom {
  @include anim-horizontal-zoom;
}

